.about-page {
    display: flex;
    flex-direction: row;
    gap: 64px;
    justify-content: center;
    align-items: center;
}

.about-photo img {
    object-fit: contain;
    max-width: 30vw;
    margin: 24px;
}

.about-text {
    max-width: 40vw;
}

.about-text h1 {
    color: #0B4912;
    font-family: IBM Plex Sans;
    font-size: 3rem;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.about-text p {
    color: #0B4912;
    font-family: IBM Plex Sans;
    font-size: 1.2rem;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.social svg {
    fill: #2A75E5;
    transition: all .1s ease-in;
}

.social svg:hover {
    fill: #0F3165;
    cursor: pointer;
}