.iframe-wrapper {
    display: flex;
    height: 100%;
}

iframe {
    flex-grow: 1;
    width:100%;
    border: none;
}

.modal-content {
    display: flex;
    flex-direction: column;
    height: 100%;

}

.modal-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.modal-header h1 {
    color: #0B4912;
    font-family: IBM Plex Sans;
    font-size: 1.5rem;
    font-style: normal;
    font-weight: 500;
    margin: 0px;
}