.app {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 64px;
}

button {
    background-color: rgba(0,0,0,0);
    border: none;
    font-family: IBM Plex Sans ;
    font-size: 1.5rem;
    font-style: normal;
    font-weight: 400;
    margin-bottom: 12px;
}

button:hover {
    color: red;
    font-weight: 600;
    cursor: pointer;
}

.emphasis {
    font-weight: 700;
}

.intro-text {
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
    max-width: 700px;
}

.intro-text h1 {
    color: #0B4912;
    font-family: IBM Plex Sans;
    font-size: 3rem;
    font-style: normal;
    font-weight: 500;
    margin: 0px;
}

.intro-text p {
    color: #0B4912;
    font-family: Hanuman;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 130%; /* 36.4px */
    margin: 0px;
}

.project-cards {
    display: flex;
    flex-direction: column;
    gap: 40px;
}

.project-card {
    height: 280px;
    width: 700px;
    display: flex;
    flex-direction: row;
    padding: 24px;
    align-items: center;
    border: 5px solid #c3c3c3;
    transition: all .1s ease-in;
    /* box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.30); */
}

.project-card-image {
    object-fit: contain;
    max-width: 300px;
    margin: 24px;
}

a {
    text-decoration: none;
}

.project-card-text {
    gap: 12px;
}

.project-card-text > a {
    text-decoration: none;
    text-decoration-style: none;
    text-align: left;
}

.project-card-text > p {
    color: #000;
    font-family: Hanuman;
    font-size: 1rem;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    text-decoration: none;
    text-align: left;
}


.project-card-text h3 {
    color: #000;
    font-family: Hanuman;
    font-size: 1.5rem;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    margin: 0;
    text-align: left;
}