.nav-bar {
    display: flex;
    height: 100px;
    padding: 0px 50px;
    justify-content: flex-end;
    align-items: center;
    gap: 70px;
    flex-shrink: 0;
}

.nav-item {
    text-decoration: none;
    color: #0D3B11;
    font-family: Helvetica;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-transform: uppercase;
}

.active {
    font-weight: 700;
}